.nav{
  display: flex;
  gap: 2rem;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 5px;
  box-shadow: 0px 2px 0px 0px #dfdddd;
}
.logo{
  display: flex;
  gap: 1rem;
  align-content: center;
}
.logotext{
  padding-top: 15px;
}
.logoimg{
  border-radius: 27px
}
img{
  border-radius: 27px;
}
.help{
  padding-top: 15px;

}
.account{
  padding-top: 15px;
}
.body{
  padding-top: 1%;
  background-color: #dadada;
  display: grid;
  grid-template-columns: 1fr 3fr 2fr;
  /* height: ; */
}
.sidebar{
  display: grid;
  gap: 1rem;
  height: 600px;
}
.item{
  text-align: center;
  background-color: white;
  border-radius: 7px;
  padding: 15px;
}
.card{
  background-color: white;
  /* border:2px solid black; */
  border-radius: 7px;
  margin: 25px;
  margin-top: 0;
}
.header{
  padding-left: 9px;
  display: flex;
  gap: 31rem;
  border-bottom: 2px solid black;
  padding-bottom: 5px;
}
.cardbody{
  display: grid;
  padding-left: 9px;
  grid-template-columns: 6fr 1fr;
  gap: 1rem;
}
.category{
  /* margin-left: 9px; */
  margin-bottom: 6px;
  background-color: #76dd76;
  border-radius: 7px;
  padding: 6px;
  width: fit-content;
}
input{
  padding: 10px;
  width: 60%;
  border-top-style: none;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-left-style: none;
  border-right-style: none;
  background-color: #dadada;
  font-size: large;
  text-align: center;
}
input:focus{
  outline: none;
  background-color: white;
  transition: background-color 0.5s;
}
.rightbar{
  text-align: center;
  /* display: grid; */
  /* gap: 1rem; */
}
.action{
  margin-top: 15px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
}
button{
  padding: 10px;
  background-color: white;
  border: none;
  border-radius: 17px;
}
.cashin{
  background-color: #76dd76;
}
.cashout{
  background-color: rgb(230, 88, 88);
}
.categorysection{
  margin-top: 15px;
  margin-left: 41px;
  display: flex;
  gap: 4rem;
}
.cateitem{
  padding: 2%;
  /* background-color: aqua; */
  border: 1px solid grey;
  cursor: pointer;
  border-radius: 17px;
  transition: background-color 0.5s;
}
.analysis{
  margin-top: 30px;
  padding: 2%;
  background-color: white;
  border-radius: 17px;
  width: 146px;
  /* border: 1px solid green; */
}